 ***************/

/* Variables */
:root {
  --blue: #2d46b9;
  --white: #ffffff;
  --black: #000000;
  --green1: #1ed760;
  --green2: #1db954;
  --gray1: #c1c3c6;
  --gray2: #919496;
  --gray3: #616467;
  --hero-bg-color: #ffc362;
  --hero-terms-link: #efefef;
}

/* Overwrite browser defaults */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'DM Sans', sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

/********** 
 * FOOTER *
 **********/

.footer {
  background: var(--black);
  padding-top: 5rem;
  padding-bottom: 2rem;
}

.footer-nav {
  padding: 0 1.5rem;
  margin: 0 auto;
}

.logo-footer {
  padding-bottom: 5rem;
}

.logo-footer img {
  width: 9rem;
}

.company-links,
.communities-links {
  margin-bottom: 6rem;
}

.company-links li,
.communities-links li,
.useful-links li {
  color: var(--white);
  margin-bottom: 2rem;
  font-size: 1.5rem;
  font-weight: 500;
}

.company-links li a:hover,
.communities-links li a:hover,
.useful-links li a:hover {
  color: var(--green1);
}

.company-links li:first-child,
.communities-links li:first-child,
.useful-links li:first-child {
  color: var(--gray2);
  font-weight: 700;
  font-size: 1.3rem;
  letter-spacing: 0.5px;
  margin-bottom: 3rem;
}

.footer-nav li:last-child {
  margin-bottom: 0;
}

.social-icons {
  padding-top: 14rem;
  padding-bottom: 1rem;
}

.social-icons ul {
  display: flex;
}

.social-icons ul li {
  margin-right: 1.5rem;
  padding: 1rem;
  background: #222326;
  border-radius: 50%;
}

.social-icons img {
  width: 2.3rem;
  background: var(--white);
  display: block;
}

.social-icons img:hover {
  background: var(--green1);
}

.country {
  text-align: right;
  margin-bottom: 1rem;
}

.country a {
  display: inline-block;
}

.country a:hover > span {
  color: var(--green1);
}

.country span {
  color: var(--gray2);
  vertical-align: super;
}

.country img {
  width: 2.5rem;
  margin-left: 1rem;
}

.bottom-links {
  display: flex;
  justify-content: space-between;
}

.bottom-links ul {
  display: flex;
  flex-wrap: wrap;
  flex: 1 1;
}

.bottom-links ul li {
  margin-right: 2.6rem;
  padding: 1rem 0;
}

.bottom-links span {
  padding: 1rem 0;
}

.bottom-links a,
.bottom-links span {
  color: var(--gray2);
}

.bottom-links a:hover {
  color: var(--green1);
}

/********
 * HERO *
 ********/

.hero {
  background: var(--hero-bg-color);
  position: relative;
}

.hero-container {
  padding: 24.5rem 1.5rem 0 1.5rem;
}

.hero-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero-title {
  font-size: 5rem;
  color: var(--white);
  text-align: center;
  font-weight: 700;
  margin-bottom: 4rem;
  line-height: 5rem;
  letter-spacing: -0.3rem;
}

.hero-button {
  padding: 2rem 6rem;
  border: none;
  border-radius: 3rem;
  color: var(--white);
  background: var(--green2);
  font-size: 1.5rem;
  font-weight: 700;
  letter-spacing: 0.2rem;
  outline: none;
  cursor: pointer;
  transition: all 0.4s ease;
  margin-bottom: 3rem;
}

.hero-button:hover {
  background: var(--green1);
}

.terms {
  display: flex;
  margin-bottom: 2rem;
}

.hero-terms-link,
.hero-terms-link + span,
.terms > span {
  font-size: 1.2rem;
  color: var(--hero-terms-link);
}

.hero-terms-link,
.terms > span {
  margin-right: 0.2rem;
}

.hero-terms-link {
  text-decoration: underline;
  transition: all 0.6s ease;
}

.hero-terms-link:hover {
  color: var(--white);
}

.headerL {
  height: 5.5rem;
  background: var(--black);
  width: 100%;
  position: fixed;
  z-index: 999;
}

.header-container {
  display: flex;
  justify-content: space-between;
  max-width: 76.5rem;
  height: 100%;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.header-logo {
  display: flex;
  align-items: center;
  height: 100%;
  cursor: pointer;
  margin-right: 15px;
}

.header-logo img {
  width: 9rem;
}

.nav-mobile {
  display: flex;
  align-items: center;
  position: relative;
}

.profileL {
  border: 2px solid var(--white);
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1.5rem;
  cursor: pointer;
}

.profileL svg {
  width: 1.6rem;
  fill: var(--white);
}

.profileL:hover {
  border-color: var(--green1);
}

.profileL:hover > svg {
  fill: var(--green1);
}

.toggler {
  width: 2rem;
  height: 3rem;
  cursor: pointer;
  z-index: 2;
  opacity: 0;
}

.hamburger-menu {
  width: 2rem;
  height: 3rem;
  position: absolute;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hamburger-menu div {
  position: relative;
  transition: all 0.4s ease;
}

.hamburger-menu div,
.hamburger-menu div::before,
.hamburger-menu div::after {
  content: '';
  height: 0.3rem;
  width: 100%;
  background: var(--white);
  border-radius: 1rem;
}

.hamburger-menu div::before {
  position: absolute;
  top: -0.7rem;
}

.hamburger-menu div::after {
  position: absolute;
  top: 0.7rem;
}

.toggler:checked + .hamburger-menu > div {
  transform: rotate(135deg);
}

.toggler:checked + .hamburger-menu > div::before,
.toggler:checked + .hamburger-menu > div::after {
  top: 0;
  transform: rotate(90deg);
}

.menu-overlay {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: fixed;
  width: 0;
  opacity: 0;
  height: 100%;
  background: var(--black);
  top: 0;
  right: 0;
  overflow-y: scroll;
  overflow-x: hidden;
  visibility: hidden;
  padding: 4rem;
  transition: all 0.3s ease;
  z-index: 999;
}

.background-overlay {
  position: absolute;
  width: 1000rem;
  height: 100vh;
  background: rgba(0, 0, 0, 0.7);
  top: 0;
  right: 0;
  display: none;
}

.toggler:checked + .hamburger-menu + .menu > .background-overlay {
  display: inline;
  display: initial;
}

.toggler:checked + .hamburger-menu + .menu > .menu-overlay {
  visibility: visible;
  width: 44.7rem;
  max-width: 100vw;
  opacity: 1;
}

.toggler:checked {
  z-index: 1001;
}

.toggler:checked + .hamburger-menu {
  z-index: 1000;
}

.menu-overlay li,
.menu-overlay-logo {
  opacity: 0;
  transform: translateX(100%);
  transition: all 0.5s ease;
}

.menu-overlay li {
  color: var(--white);
  font-size: 3.8rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.toggler:checked + .hamburger-menu + .menu > .menu-overlay li,
.toggler:checked + .hamburger-menu + .menu > .menu-overlay .menu-overlay-logo {
  transform: translateX(0);
  opacity: 1;
}

.menu-overlay li:nth-child(1) {
  transition: transform 0.1s ease;
}

.menu-overlay li:nth-child(2) {
  transition: transform 0.2s ease;
}

.menu-overlay li:nth-child(3) {
  transition: transform 0.3s ease;
}

.menu-overlay li:nth-child(4) {
  transition: transform 0.4s ease;
}

.menu-overlay li:nth-child(5) {
  transition: transform 0.5s ease;
}

.menu-overlay li:nth-child(6) {
  transition: transform 0.6s ease;
}

.menu-overlay-logo {
  transition: transform 0.6s ease;
}

.menu-overlay li a:hover {
  color: var(--blue);
}

.menu-overlay li:nth-child(4) {
  width: 2rem;
  height: 0.15rem;
  margin: 4rem 0;
  background: var(--white);
}

.menu-overlay li:nth-last-child(-n + 2) {
  font-size: 2.8rem;
  font-weight: 400;
}

.menu-overlay li:last-child {
  margin: 2rem 0;
}

.menu-overlay-logo img {
  width: 9rem;
  cursor: pointer;
}

.nav-desktop {
  display: none;
}

.nav-desktop ul {
  display: flex;
  align-items: center;
}

.nav-desktop ul li {
  font-size: 1.5rem;
  color: var(--white);
  margin-right: 3rem;
  font-weight: 700;
}

.nav-desktop > ul li:last-child {
  width: 1.7rem;
  height: 0.1rem;
  background: var(--white);
  transform: rotate(90deg);
}

.nav-desktop ul li a:hover {
  color: var(--blue);
}

.profileL-container {
  display: flex;
  cursor: pointer;
  position: relative;
}

.profileL-container ul li:first-child {
  margin-right: 1rem;
}

.profileL-container ul li:last-child {
  margin-right: 0;
}

.profileL-arrow {
  width: 1.2rem;
  fill: var(--white);
}

.dropdown-menu {
  width: 16rem;
  height: 8rem;
  position: absolute;
  background: var(--white);
  bottom: -9.5rem;
  right: -2rem;
  border-radius: 0.4rem;
  visibility: hidden;
}

.dropdown-menu-toggler {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

.dropdown-menu::before {
  content: '';
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  top: -1.3rem;
  right: 1rem;
  border-left: 1.6rem solid transparent;
  border-right: 1.6rem solid transparent;
  border-bottom: 1.6rem solid var(--white);
}

.dropdown-menu ul {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 1.5rem;
}

.dropdown-menu ul li:first-child {
  margin-bottom: 1rem;
  color: var(--black);
}

.dropdown-menu ul li:last-child {
  margin-bottom: 1rem;
  color: var(--gray2);
}

.dropdown-menu-toggler:checked + .dropdown-menu {
  visibility: visible;
}

.dropdown-menu-toggler:checked
  + .dropdown-menu
  + .profileL
  + ul
  li:last-child
  svg {
  transform: rotate(180deg);
}

/* These styles will be applied to screens that have a width of at least 768px */
@media screen and (min-width: 768px) {
  /* HERO */
  .hero-container {
    padding-top: 0;
    min-height: 64rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: radial-gradient(
      ellipse at center,
      #fbcb6c 0%,
      #f3c65d 0%,
      #f2ae54 100%
    );
  }

  .hero-title {
    font-size: 6.5rem;
    line-height: 8rem;
    letter-spacing: -0.4rem;
  }

  .hero-button {
    margin-bottom: 0;
  }

  .terms {
    position: absolute;
    bottom: 1.2rem;
  }

  /* ALBUMS */
  .albums-container {
    width: 75rem;
    margin: 0 auto;
  }

  .albums-cards-container {
    flex-wrap: wrap;
    overflow: visible;
    padding: 5rem 0;
  }

  .album {
    min-width: 0;
    width: 33.33%;
    box-sizing: border-box;
    padding: 0.8rem;
    position: relative;
  }

  .album:first-child,
  .album:last-child {
    padding: 0.8rem;
  }

  .album > img {
    height: 100%;
  }

  .albums-content {
    padding-bottom: 0;
  }

  /* Looking for music? */
  .albums-content h2 {
    font-size: 4rem;
  }

  .album-info {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    justify-content: center;
    opacity: 0;
    margin: 0.8rem;
    transition: all 0.4s ease;
    cursor: pointer;
  }

  .album-info h4 {
    margin-top: 0.5rem;
    font-weight: 400;
    letter-spacing: 0.005rem;
  }

  .album-info a {
    position: relative;
    bottom: -2rem;
  }

  .album-info:hover {
    opacity: 1;
    background: rgba(0, 0, 0, 0.8);
  }

  .album-info:hover > h2 {
    color: var(--white);
    white-space: normal;
    overflow: visible;
    overflow: initial;
    text-overflow: initial;
  }

  /* FOOTER */
  .footer-nav {
    width: 75rem;
  }

  .top-links {
    display: flex;
    justify-content: space-between;
  }

  .bottom-links ul li {
    font-size: 1.16rem;
  }

  .country span,
  .bottom-links span {
    font-size: 1.2rem;
  }
}

/* These styles will be applied to screens that have a width of at least 992px */
@media screen and (min-width: 992px) {
  /* HEADER */
  .headerL {
    background: rgba(0, 0, 0, 0.6);
    height: 8rem;
  }

  .header-container {
    max-width: 97rem;
  }

  .header-logo img {
    width: 13rem;
  }

  .nav-desktop {
    display: flex;
    align-items: center;
  }

  .nav-mobile {
    display: none;
  }

  /* Circle around the profile icon */
  .profileL {
    width: 4rem;
    height: 4rem;
  }

  /* Profile icon */
  .profileL svg {
    width: 2rem;
  }

  /* HERO */
  .hero-title {
    font-size: 8rem;
    margin-bottom: 0;
  }

  .hero-content {
    width: 70%;
  }

  .hero-button {
    position: relative;
    top: 6rem;
  }

  /* ALBUMS */
  .albums-container {
    width: 97rem;
  }

  .albums-cards-container {
    padding-top: 7rem;
    padding-bottom: 13rem;
  }

  /* Looking for music */
  .albums-content h2 {
    font-size: 4.5rem;
  }

  .album-info {
    padding: 0 1rem;
  }

  .album-info h4 {
    font-size: 1.6rem;
  }

  /* FOOTER */
  footer {
    padding-top: 8rem;
    padding-bottom: 5rem;
  }

  .footer-nav {
    width: 97rem;
    position: relative;
  }

  .logo-footer,
  .top-links,
  .social-icons {
    vertical-align: top;
  }

  .logo-footer {
    display: inline-block;
    width: 16.666666%;
  }

  .logo-footer img {
    width: 13rem;
  }

  .top-links {
    display: inline-flex;
    width: 50%;
  }

  .social-icons {
    display: inline-block;
    margin: 0 1.5rem 0 auto;
    padding-top: 0;
    position: absolute;
    top: 0;
    right: 0;
  }

  .social-icons ul li {
    padding: 1.5rem;
  }
}

/* These styles will be applied to screens that have a width of at least 1200px */
@media screen and (min-width: 1200px) {
  /* HEADER */
  .header-container {
    max-width: 117rem;
  }

  /* HERO */
  .hero-button {
    top: 5rem;
  }

  /* ALBUMS */
  .albums-container {
    width: 117rem;
  }

  .album-info h2 {
    font-size: 3rem;
  }

  .album-info h4 {
    font-size: 1.8rem;
  }

  /* FOOTER */
  .footer-nav {
    width: 117rem;
  }
}

